.MainBlock {
    min-height: 100%;
    /*background-color: #1f1f20;*/
    display: flex;
    justify-content: center;
    /*padding: 80px 0;*/
    /*padding: 100px;*/

}

.container {
    /* border: 1px solid;  */
    width: 80%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 250px;
    margin-bottom: 250px;
}

.block {
    justify-content: space-around;
    display: flex;
    /* border: 1px solid; */
    flex-wrap: wrap;
    /*padding: 100px;*/
}

.greeting {

    /* border: 1px solid; */
    display: flex;
    flex-direction: column;
    align-self: center;
    color: white;
    z-index: 0;
}

.greeting span:nth-child(1) {
    font-size: 16px;
}


.greeting span:nth-child(2),
.greeting span:nth-child(2) span {
    font-weight: 500;
    font-stretch: normal;
    font-size: 40px;
    line-height: 1.2;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 5px;
    position: relative;
    z-index: 0;

}

.greeting span:nth-child(2) span::after {

    content: "";
    position: absolute;
    width: 100%;
    height: 20px;
    background-color: #4e93e6;
    left: 0;
    right: 0;
    bottom: 0;

    z-index: -1;
    height: 15px;

}

.greeting h1 {
    font-size: 26px;}


.greeting:hover{
    transform: scale(1.2,1.2);
    transition: 755ms;
}


.photo {
    /*border: 1px solid;*/
    margin-top: 50px;
    position: relative;

    z-index: 9999999999;

}
.photo:hover{
    transform: scale(1.3,1.3);
    transition: 755ms;
}

.image {
    background-color: #4e93e6;
    background-image: url("https://instagram.fhel5-1.fna.fbcdn.net/v/t51.2885-15/e35/s1080x1080/71027220_439810513310915_1117345521579983985_n.jpg?_nc_ht=instagram.fhel5-1.fna.fbcdn.net&_nc_cat=111&_nc_ohc=7wmKJ6-M8-gAX876L9C&oh=8c3e0babe570b556503426e95860661a&oe=5EC8BE0A");
    width: 300px;
    height: 400px;
    position: relative;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
     /*z-index: 999;*/
}

.image {
    /*width: 250px;*/
    /*height: 300px;*/

    width: 296px;
    height: 390px;

    border-radius: 10px;
}

.photo .image:before {
    content: "";
    position: absolute;
    /*height: 330px;*/
    /*width: 250px;*/

    height: 370px;
    width: 290px;

    border: 5px solid #4e93e6;
    left: 15px;
    top: -20px;
    z-index: -11111111111111;
    border-radius: 10px;
}
.photobefore:hover{
    transform: scale(1.3,1.3);
    transition: 755ms;
}

